import React, { useState } from 'react';
import { Row, Col, Image } from 'react-bootstrap';
import { withRouter } from 'react-router';
import '../users/users.css';
import { connect } from 'react-redux';
import {customerTomorrowOrders , totalMoneyCalc } from './setting';

const ulArea ={
  marginTop: '-4%'
}
const image={
  width: '100px',
  height: '100px'
}

/**************************************************************************************** */
const  Reservation = (props)=>{
  return(
    <>  
      <Row>
        <Col md={{ span: 8, offset: 2 }} className="pt-3 pl-5 pr-5 pb-4 bg-light shadow">
          <div className="text-center"></div>
          <h3 className="mt-2">: 
            <span className="text-danger font-weight-bold">
              {totalMoneyCalc(customerTomorrowOrders(props.userData[0].orders[0]))}
            </span></h3>
          <ul class="list-group" style={ulArea}>
          {props.userData.length>0? customerTomorrowOrders(props.userData[0].orders[0]).map((data)=>(
            <li className="list-group-item mt-5">
              <Row>
                <Col md="12">
                  <ul className="list-inline">
                    <li className="list-inline-item h4 ml-5">
                       <Image src={`http://yukiabineko.sakura.ne.jp/react/${data.name}.jpg`} alt="" style={image} roundedCircle />
                    </li>
                    <li className="list-inline-item h5 mr-5">:{data.name}</li>
                    <li className="list-inline-item h5 mr-3">:<span className="text-danger">{data.price}</span></li>
                    <li className="list-inline-item h5 mr-3">:{data.num}</li>
                    <li className="list-inline-item h5 mr-3">:<span className="text-danger">{data.price *data.num}</span></li>
                    <li className="list-inline-item h5 mr-3">:{data.process}</li>
                  </ul>
                
                </Col>
              </Row>
            </li>
          )) : ''}
          </ul>
        </Col>
      </Row>
    </>
   )
}
export default withRouter(connect(state=>state)(Reservation));
/***************************************************************************************************** */